video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
  }

  .center {
    text-align: center;
    background-color: rgb(0, 0, 0);
    font-weight: bold;
    line-height: 200%;
    
  }
  
  .hero-container {
   /* background-image: url('/src/assests/images/back2.png') ; */
    background-attachment: fixed;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
     object-fit: contain;
    position: relative;
    margin-top: -80px;
    color: #fff;
    
  }
  

.hero-container > h1 {
  color: #fff;
  font-size: 100px;
  margin-top: 100px;
  margin-bottom: 100px;
}

.hero-container > p {
  margin-top: 8px;
  background-color:transparent;
  color: #fff;
  font-size: 32px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .hero-container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}