.center {
  text-align: center;
  font-weight: bold;
  background-color: dimgray;
}

.cards__container {
 /* background: url('./assets/images/img-home.jpg') center center/cover no-repeat; */
   
 width: 100%;
 background-attachment: fixed;

 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 padding: 0 1rem;
  object-fit: contain;
 position: relative;
 margin-top: -80px;
  

}


.titlepadding{
  margin-top: 150px;
  padding-bottom: 150px;
  font-size: 30px;
  font-weight: 700;

}



  
  h1 {
    text-align: center;
    font-size: 30px;
  font-weight: 700;
  }

  
  
  
  .cards__wrapper {
     position: relative;
    margin: 50px 0 45px;
  }
  
  .cards__items {
    margin-bottom: 24px;
   }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    margin-top: 20px;
    border-radius: 10px;
    margin-left: 20px;
    margin-right: 20px;
    background-color: white;
    width: 500px;
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 0px 25px rgb(0, 0, 0);
    -webkit-filter: drop-shadow(0 6px 20px rgba(255, 230, 0, 0.017));
    filter: drop-shadow(0 6px 20px rgba(253, 255, 117, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
   }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
   }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 18px;
    font-weight: 700;
    color: #fff;
    background-color: #3ea4e7;
    box-sizing: border-box;
  }
  
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
    
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
    background-color: white;
  }
  
  .cards__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
  }